<template>
  <div class="new">
    <van-nav-bar class="nav" title="新闻详情" left-arrow @click-left="goback" />
    <p class="headline">{{ news.title }}</p>
    <span class="time">发布时间：{{ news.time }}</span>
    <van-divider dashed />
    <div class="context">
      <p v-html="news.context">{{ news.context }}</p>
      <p class="publisher">作者：{{ news.publisher }}</p>
    </div>
    <van-divider dashed />
    <div class="lastNext">
      <p>
        <span
          >上一条：<a @click="lastAndNext(last.id)" v-if="last">{{
            last.title
          }}</a></span
        >
      </p>
      <p class="next">
        <span
          >下一条：<a @click="lastAndNext(next.id)" v-if="next">{{
            next.title
          }}</a></span
        >
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'New',
  props: ['id'],
  watch: {
    $route() {
      this.getNew()
    }
  },
  data() {
    return {
      news: {
        title: '',
        publisher: '',
        time: '',
        context: '',
        click: '',
        fileURL: ''
      },
      filename: '',
      last: {},
      next: {}
    }
  },
  methods: {
    goback() {
      this.$router.push('/news')
    },
    async getNew() {
      let res = await this.$http.get('/api/home/new?id=' + this.id)
      if (res.data.code === 200) {
        this.news = res.data.data.news
        this.filename = res.data.data.filename
        if (this.news.context.length !== 0) {
          this.news.context = this.news.context.replace(
            /img/g,
            'img style = " max-width:calc(100% - 1rem/2); "'
          )
        }
      }
      let lastRes = await this.$http.get('/api/home/newlast?id=' + this.id)
      if (lastRes.data.code === 200) {
        this.last = lastRes.data.data
      }
      let nextRes = await this.$http.get('/api/home/newnext?id=' + this.id)
      if (nextRes.data.code === 200) {
        this.next = nextRes.data.data
      }
    },
    lastAndNext(id) {
      this.$router.push('/news/detail/' + id)
    }
  },
  mounted() {
    this.getNew()
  }
}
</script>

<style scoped>
.new {
  max-width: 100%;
  overflow: hidden;
}
.nav {
  background-color: #333744;
}
.headline {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
  margin-top: 0.5rem;
}
.publisher {
  margin-left: 0.5rem;
  margin-top: 0.5rem;
  color: #808080;
}
.time {
  color: #808080;
  margin-top: 0.75rem;
  font-size: 0.8125rem;
  margin-left: 0.5rem;
}
.context {
  min-width: 100%;
  margin: 0 0.5rem;
  min-height: 23.125rem;
  line-height: 150%;
}
.lastNext {
  margin-left: 0.5rem;
}
.next {
  margin-top: 1.25rem;
}
</style>
<style>
.van-nav-bar__title {
  color: #fff;
}
.van-nav-bar .van-icon {
  color: #fff;
}
</style>
